.reviews-container {
  min-width: 1110px;
  min-height: 465px;
  text-align: center;
  padding: 140px 165px;
  position: relative;

  .cards-container {
    display: flex;
  }

  .burger {
    position: absolute;
    left: 0;
    top: 0;
  }

  .bottom-logo {
    position: absolute;
    right: 0;
    bottom: 0;
  }

  h1:first-of-type {
    color: white;
    font-size: rem(48px);
    line-height: rem(48px);

    span {
      color: #79c8c7;
    }
  }
  h2 {
    color: #79c8c7;
  }

  .card {
    max-width: 350px;
    margin: 30px;
    background-image: url("../images/icon-quotes.svg");
    background-position: 50% 0;
    background-repeat: no-repeat;

    h1:first-of-type {
      color: white;
      font-size: rem(32px);
      line-height: rem(32px);
      width: 327px;
      height: 128px;
      margin: 0 auto;
      span {
        color: #79c8c7;
      }
    }

    img {
      width: 62px;
      height: 62px;
      border: 2px solid #c4fffe;
      border-radius: 40px;
    }

    p {
      color: white;
    }

    .review {
      height: 100px;
      margin-bottom: 120px;
    }

    .profession {
      font-style: italic;
      font-size: rem(13px);
    }
  }
}

@media screen and (max-width: 440px) {
  .reviews-container {
    min-width: 100vw;
    text-align: center;
    padding: 140px 5px;

    .burger {
      width: 147px;
      height: 100px;
      left: 0vw;
    }

    .cards-container {
      flex-direction: column;
    }

    .card {
      max-width: 350px;
      margin-bottom: 30px;
      background-image: url("../images/icon-quotes.svg");
      background-position: 50% 0;
      background-repeat: no-repeat;

      img {
        width: 62px;
        height: 62px;
        border: 2px solid #c4fffe;
      }

      p {
        color: white;
      }

      .review {
        height: 100px;
        margin-bottom: 60px;
      }

      .profession {
        font-style: italic;
        font-size: rem(13px);
      }
    }
  }
}
